import Toast from '@components/Toast';
export default {
    components: {
        Toast,
    },
    props: {
        tip: {
            type: String,
            default: '',
        },
    },
    data() {
        return {
            toastOpen: false,
            isInstallWeChat: true,
            isShowToast: false,
            toastMsg: '',
        };
    },
    mounted() {
        this.checkInstallWeChat();
    },
    methods: {
        openSelf() {
            this.toastOpen = true;
        },
        openWeixin() {
            if (!this.isInstallWeChat) {
                this.isShowToast = true;
                this.toastMsg = '';
                return;
            }
            window.lgBridge && window.lgBridge.request('openWeChat');
        },
        checkInstallWeChat() {
            window.lgBridge && window.lgBridge.request('check_install', {
                'data': {
                    'app_names': ['wechat']
                }
            }, (res) => {
                if (res.data && res.data.data) {
                    let { wechat } = res.data.data;
                    if (wechat === 0) {
                        this.isInstallWeChat = true;
                    }
                }
            });
        },
        hideToast() {
            this.isShowToast = false;
        },
    },
};
