import Modal from '@components/Login/Modal';
import defaultAvatar from '@assets/course-list/pc/default-avatar@2x.png';
import { API_BASE_URL } from '@/common/js/util/api-config.js';
import bus from '@js/util/bus.js';
import MyConfirm from '@/common/plugins/pcModal/index.js';

export default {
    name: 'Header',
    components: {
        Modal
    },
    data() {
        let userInfo = window.userInfo;
        return {
            userInfo,
            isLogin: window.isLogin,
            pathName: '',
            tabList: [
                {
                    name: '',
                    href: 'https://edu.lagou.com',
                    pathName: '/'
                },
                {
                    name: '',
                    href: 'https://edu.lagou.com/learn',
                    pathName: '/learn'
                },
                {
                    name: '',
                    href: 'https://kaiwu.lagou.com/enterprise/index.html'
                },
            ],
            tooltipList: [
                {
                    label: '',
                    target: '_blank',
                    link: 'https://webchat.7moor.com/wapchat.html?accessId=d2399a80-0b6e-11ea-a5c0-bfdef258dfcd&fromUrl=http://jiaoyu.lagou.com&urlTitle=jiaoyuPC'
                }, {
                    label: '',
                    target: '_self',
                    link: `https://edu.lagou.com/setting`
                }, {
                    label: '',
                    target: '_self',
                    link: `${window.location.origin}/frontLogout.do`
                },
            ],
            defaultAvatar,
            isShowLoginModal: false
        };
    },
    provide() {
        return {
            self: this
        };
    },
    watch: {
        '$route': {
            handler(to, from) {
                this.pathName = window.location.pathname;
                console.log('$route', this.pathName);
            },
            immediate: true
        }
    },
    mounted() {
        bus.$on('handelOpenLogin', this.goLogin);
        this.$nextTick(() => {
            sessionStorage.getItem('bodyHasDialog') && MyConfirm() && sessionStorage.removeItem('bodyHasDialog');
        });
    },
    methods: {
        go(item) {
            let { name, href } = item;
            if (name === '') {
                window.open(href);
                return;
            }
            window.location.replace(href);
        },
        goLogin() {
            this.isShowLoginModal = true;
        },
        closeLogin() {
            this.isShowLoginModal = false;
        }
    }
};
