import { copyContext } from '@js/util/util';
import { browserName } from '@js/userAgent/browser-name-and-OS.js';
import Confirm from './confirm.vue';
import { fetchCommunity, joinCommunity } from '@/course/service/index';

export default {
    components: {
        Confirm,
    },
    props: {
        courseSource: [String, Number], //1 2
        courseId: [Number, String],
    },
    data() {
        return {
            isInstallWeChat: false,
            isOpen: false,
            isComein: false,
            hasGroup: false,
            tip: '',
            phone: '',
            baseInfo: null,
        };
    },
    computed: {
        isWx() {
            //
            return browserName === 'WX';
        },
    },
    mounted() {
        this.getData();
    },
    destroyed() {
        document.removeEventListener('scroll', this.changeCommunity, true);
    },
    methods: {
        getData() {
            let params = {
                courseSource: this.courseSource,
                courseId: this.courseId,
            };
            fetchCommunity(params).then((res) => {
                if (res.state === 1) {
                    if (res.content) {
                        this.baseInfo = res.content;
                        this.isComein = this.baseInfo.inGroup;
                        this.hasGroup = true;
                        this.phone = 'tel:' + this.baseInfo.serviceTel;
                        this.$nextTick(() => {
                            document.addEventListener('scroll', this.changeCommunity, true);
                        });
                    } else {
                        this.hasGroup = false;
                    }
                }
            });
        },
        openSelf() {
            this.isOpen = true;
            this.eduPlatTrack('WebButtonClick', {
                'page_id': '1pz4',
                'button_name': '',
                'courses_id': [String(this.courseId || '')]
            });
        },
        toQuestion() {
            location.href = 'https://activity.lagou.com/topic/czsm.html';
        },
        copyContent(type, name) {
            this.eduPlatTrack('WebButtonClick', {
                'page_id': '1qnv',
                'button_name': name,
                'courses_id': [String(this.courseId || '')]
            });
            if (type === 'teacher') {
                copyContext(this.baseInfo.teacherWeChat).then(() => {
                    if (this.isWx) {
                        this.showToast(
                        ''
                        );
                    } else {
                    this.modalOpen('');
                    }
                });
            } else if (type === 'test') {
                copyContext(this.baseInfo.validationMessage).then(() => {
                    if (this.isWx) {
                        this.showToast(
                        ` ${this.baseInfo.validationMessage}`
                        );
                    } else {
                    this.modalOpen(`${this.baseInfo.validationMessage}`);
                    }
                });
            } else if (type === 'service') {
                copyContext(this.baseInfo.serviceWeChat).then(() => {
                    if (this.isWx) {
                        this.showToast(
                        ``
                        );
                    } else {
                    this.modalOpen(``);
                    }
                });
            }
        },
        modalOpen(tip) {
            this.tip = tip;
            this.$refs.confirm.openSelf();
        },
        hasComein() {
            if (this.isComein) {
                return;
            }
            this.eduPlatTrack('WebButtonClick', {
                'page_id': '1qnv',
                'button_name': '',
                'courses_id': [String(this.courseId || '')]
            });
            //
            let params = {
                courseSource: this.courseSource,
                courseId: this.courseId,
            };
            joinCommunity(params).then((res) => {
                if (res.state === 1) {
                    this.isComein = true;
                }
            });
        },
        changeCommunity() {
            let communityDom = document.querySelector('.community');
            communityDom && (communityDom.style.left = '-40px');
            clearTimeout(this.timer);
            this.timer = setTimeout(() => {
                let t2 = document.documentElement.scrollTop || document.body.scrollTop;
                if (t2 === t1) {
                    communityDom && (communityDom.style.left = '17px');
                }
            }, 1000);
            let t1 = document.documentElement.scrollTop || document.body.scrollTop;
        },
    },
};
