import { fetchPostCrmInfo } from '@/enterprise-course/service/index';
import { fetchSaveCrmInfo } from '@/campus/service';
import { message } from '@components/ResetMessage/Index';
import { checkPhone } from '@js/util/util';
import Toast from '@components/Toast';
let isCampus = window.location.pathname.includes('campus');
    export default {
        components: {
            Toast,
        },
        props: {
            showModal: {
                type: Boolean,
                default: false,
            },
            showCloseBtn: {
                type: Boolean,
                default: true
            },
            isForTeacher: {
                type: Boolean,
                default: false
            }
        },
        data() {
            let formLabel = isCampus ? {
                key: '', label: '', channelName: '', submitKey: 'campusName',
            } : {
                key: '', label: '', channelName: '', submitKey: 'companyName'
            };
            let { channelName, submitKey } = formLabel;
            return {
                showCloseIcon: false,
                isShowCompanyNameTip: false,
                isShowNameTip: false,
                isShowPhoneTip: false,
                isShowToast: false,
                toastMsg: '',
                formLabel,
                dataForm: {
                    channelName: channelName,
                    [submitKey]: '',
                    name: '',
                    phone: ''
                },
            };
        },
        watch: {
            showModal() {
                this.init();
            }
        },
        methods: {
            // ios
            resize() {
                setTimeout(() => {
                    window.scrollTo(0, 0);
                }, 0);
            },
            init() {
                this.dataForm.name = '';
                this.dataForm.phone = '';
                this.dataForm.campusName = '';
                this.dataForm.companyName = '';
            },
            closeModal() {
                this.$emit('close', true);
            },
            headleInput(val) {
                console.log(val);
                this.showCloseIcon = true;
                this[val] = false;
            },
            clearInput(val) {
                this.dataForm[val] = '';
            },
            hideToast() {
                this.isShowToast = false;
            },
            submit(e) {
                e.preventDefault();
                let { name, phone, companyName, campusName } = this.dataForm;
                name = name.trim();
                phone = phone.trim();
                let typeName = this.dataForm[this.formLabel.submitKey].trim();
                if (!name) {
                    this.isShowNameTip = true;
                }
                if (!phone || !checkPhone(phone)) {
                    this.isShowPhoneTip = true;
                }
                if (!typeName) {
                    this.isShowCompanyNameTip = true;
                }
                if (this.isShowNameTip || this.isShowPhoneTip || this.isShowCompanyNameTip) return;
                let crmAction = isCampus ? fetchSaveCrmInfo : fetchPostCrmInfo;
                crmAction(this.dataForm).then(res => {
                    this.isShowToast = true;
                    this.toastMsg = '';
                    this.$emit('submit');
                }).catch(e => {
                    this.isShowToast = true;
                    this.toastMsg = '';
                    this.$emit('submit');
                });
                this.resize();
            }
        },
    };
