import SpeedComponent from '../AliPlayerComponents/RateComponent';
import QualityComponent from '../AliPlayerComponents/QualityComponent';

export default class ALIPlayer {
    constructor(options) {
        this.options = options;
    }

    videoOptions() {
        const { playbackRateMenuButton, vid, playauth, id, encryptMedia, thumbnailUrl } = this.options;
        const componentsArr = playbackRateMenuButton ? [{
            name: 'QualityComponent',
            type: QualityComponent
        }, {
            name: 'SpeedComponent',
            type: SpeedComponent
        }] : [{
            name: 'QualityComponent',
            type: QualityComponent
        }];
        return Object.assign({}, {
            'id': 'player-con',
            'autoplay': true,
            'isLive': !playbackRateMenuButton,
            'rePlay': false,
            'playsinline': true,
            'preload': true,
            'controlBarVisibility': 'hover',
            'useH5Prism': true,
            'cover': '',
            'width': '100%',
            'height': '100%',
            components: componentsArr,
            'skinLayout': [
                { name: 'bigPlayButton', align: 'blabs', x: 30, y: 80 },
                { name: 'H5Loading', align: 'cc' },
                { name: 'errorDisplay', align: 'tlabs', x: 0, y: 0 },
                { name: 'tooltip', align: 'blabs', x: 0, y: 56 },
                { name: 'thumbnail' },
                {
 name: 'controlBar', align: 'blabs', x: 0, y: 0,
                    children: [
                        { name: 'progress', align: 'blabs', x: 0, y: 40 },
                        { name: 'playButton', align: 'tl', x: 15, y: 12 },
                        { name: 'volume', align: 'tl', x: 15, y: 10 },
                        { name: 'timeDisplay', align: 'tl', x: 15, y: 4 },
                        { name: 'fullScreenButton', align: 'tr', x: 10, y: 10 }
                    ]
                }
            ],
            definition: '240',
            defaultDefinition: 'HD',
        }, {
            // vid,
            // playauth,
            // id,
            ...this.options,
            encryptType: encryptMedia ? 1 : 0
            // thumbnailUrl,
            // withCredentials: true,

        });
    }

    playingButtonStyle() {
        $('.prism-big-play-btn').hide();
        $('.prism-play-btn').addClass('playing');
    }

    pauseButtonStyle() {
        $('.prism-big-play-btn').show();
        $('.prism-play-btn').removeClass('playing');
    }

    getPlayer() {
        let that = this;
        let options = that.videoOptions();
        return new Promise((resolve, reject) => {
            let player = new window.Aliplayer(options);
            player.on('pause', () => {
                this.pauseButtonStyle();
            });
            player.on('play', () => {
                this.playingButtonStyle();
            });
            player.on('click', ({ target }) => {
                if (target.nodeName !== 'VIDEO') {
                    return;
                }
                if (player.getStatus() === 'pause') {
                    player.play();
                } else {
                    player.pause();
                }
            });
            resolve(player);
        });
    }

    setCurrentTime(currentTime, player) {
        console.log('setCurrentTime+++++', currentTime);
        player.seek && player.seek(currentTime);
    }

    getCurrentTime(player) {
        return player.getCurrentTime();
    }

    getDuration(player) {
        return player.getDuration();
    }

    getVolume(player) {
        console.log(player, 'aliplayer', player.getVolume);
        return player.getVolume();
    }

    setVolume(vol, player) {
        console.log(player.setVolume, vol, 'player.setVolume');
        player.setVolume && player.setVolume(vol);
    }

    loadVideo(options, player) {
        player && player.replayByVidAndPlayAuth(options.vid, options.playauth);
    }

    pause(player) {
        player && player.pause();
    }
    exitFullscreen(player) {
        player.cancelFullScreen();
    }
    replay(player) {
        console.log('replay++++++++++');
        player.seek && player.seek(0);
        player.play();
    }
    dispose(player) {
        player.dispose();
    }
}
