export default {
    name: 'Dialog',
    props: {
        show: {
            type: Boolean,
            default: false
        },
        title: {
            type: String,
            default: ''
        },
        content: {
            type: String,
            default: ''
        },
        showCancelButton: {
            type: Boolean,
            default: true
        },
        showConfirmButton: {
            type: Boolean,
            default: true
        },
        cancelBtnText: {
            type: String,
            default: ''
        },
        confirmBtnText: {
            type: String,
            default: ''
        }
    },
    data() {
        return {};
    },
    methods: {
        close(e) {
            e.stopPropagation();
            this.$emit('close');
        },
        cancleFn() {
            this.$emit('onCancle');
        },
        confirmFn() {
            this.$emit('onConfirm');
        }
    }
};
