import bus from '@js/util/bus.js';
export default {
    name: 'PcModal',
    data() {
        return {
            show: true,
            ip: ''
        };
    },
    mounted() {
        this.ip = sessionStorage.getItem('otherIp');
        this.$message.warning('');
    },
    methods: {
        close() {
            this.show = false;
            sessionStorage.removeItem('bodyHasDialog');
        },
        login() {
            this.close();
            bus.$emit('handelOpenLogin');
        }
    }
};
