let toString = Object.prototype.toString;

function isArray(val) {
    return toString.call(val) === '[object Array]';
}

function isFunction(val) {
    return toString.call(val) === '[object Function]';
}

let addStyleJs = (function(dom) {
    let cache = {}; /*cssjs*/
    let loadSourceLength = 1; /* */
    let completedLoadNum = 0;
    /**
     * 
     * @head {String}  head 
     * @dom {String}   script  link 
     * @callback {Function} 
     * */
    function listenerSourceStateChange(head, dom, callback) {
        head.appendChild(dom); /*HTML*/
        dom.onload = dom.onreadystatechange = function() { /**/
            if (!this.readyState || this.readyState === 'loaded' || this.readyState === 'complete') {
                dom.onload = dom.onreadystatechange = null;
                callback();
            }
        };
    }
    /**
     * HTML  CSS
     * @path {String}  link .css
     * @callback {Function} 
     * */
    function addCss(path, callback) {
        /**/
        if (checkCache(path)) {
            callback();
            return;
        }
        let head = dom.getElementsByTagName('head')[0];
        let link = dom.createElement('link');
        link.href = path;
        link.rel = 'stylesheet';
        link.type = 'text/css';
        listenerSourceStateChange(head, link, callback);
        cache[path] = 1; /*css,1*/
    }
    /**
     * HTMLjs
     * @path {String} src .js
     * @callback {Function} 
     * */
    function addJs(path, callback) {
        /**/
        if (checkCache(path)) {
            callback();
            return;
        }
        let head = dom.getElementsByTagName('head')[0];
        let script = dom.createElement('script');
        script.src = path;
        script.type = 'text/javascript';
        listenerSourceStateChange(head, script, callback);
        cache[path] = 1; /*js,1*/
    }
    /**
     * 
     * @path {String} 
     * */
    function checkCache(path) {
        return !!cache[path];
    }
    /**
     * HTMLcssjs
     * @path {String} src  link .js.css
     * @callback {Function} 
     * */
    return function(path, callback) {
        if (!path) { /**/
            throw new Error('path!');
        }
        let source = [path];
        if (isArray(path)) {
            loadSourceLength = path.length;
            source = path;
        }
        function checkComplete() {
            completedLoadNum++;
            if (completedLoadNum === source.length) {
                console.log('completed');
                isFunction(callback) && callback();
                completedLoadNum = 0;
            }
        }
        source.forEach(item => {
            if (item.substr(-3) === '.js') { /*.js*/
                addJs(item, checkComplete);
            } else if (item.substr(-4) === '.css') { /*.css*/
                addCss(item, checkComplete);
            } else {
                throw new Error('path!');
            }
        });
    };
})(document);

export default addStyleJs;
