import Toast from '@components/Toast/index';
import { fetchPhoneCode, submitPassWord } from '@service/login';
import CountryCodeList from '@/common/components/Login/CountryCodeList';
import { OSName } from '@/common/js/userAgent/browser-name-and-OS';
import mixin from './mixin';
export default {
    components: {
        CountryCodeList,
        Toast,
    },
    mixins: [mixin],
    props: {
        fetchVerifycode: {
            type: Function,
            default: fetchPhoneCode
        },
        verifyCodeParams: {
            type: Object,
            default: () => {
                return {
                };
            }
        },
        hideButton: {
            type: Boolean,
            default: false
        },
        needRefreshPage: {
            type: Boolean,
            default: false
        },
        loginSuccessCallback: {
            type: Function,
            default: function () {}
        },
        blur: {
            type: Function,
            default: function () {
                if (OSName === 'iOS') {
                    setTimeout(() => {
                    const scrollHeight = document.documentElement.scrollTop || document.body.scrollTop || 0;
                        window.scrollTo(0, Math.max(scrollHeight - 1, 0));
                    }, 0);
                }
            }
        },
        changefocus: {
            type: Function,
            default: function (el) {
               if (OSName === 'Android') {
                   // 
                   window.onresize = () => {
                        el.target.scrollIntoViewIfNeeded(true);
                        el.target.scrollIntoView(true);
                   };
                }
            }
        }
    },
    data() {
        return {
            isLoginSuccess: false,
            flag: false, // 
        };
    },
    computed: {
        inputIsFull: data => data.phoneValue.length > 0 && data.codeValue.length > 0,
    },
    watch: {
        phoneValue(data) {
            this.showClearInput = data.length > 0;
            if (this.errorTips.show) {
                this.setErrorTip();
            }
        },
        codeValue() {
            if (this.errorTips.show) {
                this.setErrorTip();
            }
        }
    },
    methods: {
        refreshPage() {
            window.location.reload();
        },
        // 
        goSale() {
            console.log('loginSuccessCallback++++++++');
            this.$emit('loginSuccessCallback');
            this.flag = false;
        },
        hideToast() {
            this.errorTips.show = false;
        },
        setErrorTip(text = '') {
            let form = document.querySelector('.inside-form');
            let container = document.querySelector('.launch-container');
            if (form && container) {
                container.scrollTo(0, form.offsetTop / 2);
            }
            let show = !!text;
            this.errorTips = { show, text };
        }
  }
};
