/**
 * APP
 *
 * APPwindowgetShareInfo http://git.lagou.com/lg-web/lg-server-document/blob/master/technical/archive/H5.md
 *
 * [enableAppShare/changeAppShare/disableAppShare description]
 * @param  {Object}   config      []
 *     config.shareUrl      {[String]}      []
 *     config.title         {[String]}      []
 *     config.desc          {[String]}      []
 *     config.imgUrl        {[String]}      [URL]
 *     config.picUrl        {[String]}      [URLQQAPP v6.1.0]
 *     config.originalId    {[String]}      [OriginalIdAPP v6.1.0]
 *     config.minaTitle     {[String]}      [APP v6.1.0]
 *     config.minaImgUrl    {[String]}      [APP v6.1.0]
 *     config.minaPath      {[String]}      [APP v6.1.0]
 * @param  {[Function]} callback  [//]
 * @return {Object}               []
 *
 */

let shareInfo = {};

export function enableAppShare(config) {
    shareInfo = Object.assign(shareInfo, config);
    let isDefaultFunction = false;
    if (window.getShareInfo && window.getShareInfo.toString() === 'function() {}') {
        isDefaultFunction = true;
    }
    window.getShareInfo = !isDefaultFunction && window.getShareInfo || function() {
        const obj = {
            share_url: shareInfo.shareUrl || window.location.href,
            share_title: shareInfo.title || '',
            share_desc: shareInfo.desc || '',
            share_imgUrl: shareInfo.imgUrl || '',
            share_picUrl: shareInfo.picUrl || '',
            share_minaOriginalId: shareInfo.originalId || '',
            share_minaTitle: shareInfo.minaTitle || '',
            share_minaImgUrl: shareInfo.minaImgUrl || '',
            share_minaPath: shareInfo.minaPath || ''
        };
        for (const i in obj) {
            if (!obj[i]) {
                delete obj[i];
            }
        }
        return obj;
    };
    return shareInfo;
}

export function changeAppShare(config) {
    shareInfo = Object.assign(shareInfo, config);
    return shareInfo;
}

export function disableAppShare() {
    delete window.getShareInfo;
    return shareInfo;
}
