let md5 = require('md5');
import axios from '@service/index';
import { API_BASE_URL } from '@js/util/api-config.js';
import { getParam } from '@js/util/getParam';
import CountryCodeList from '@components/Login/CountryCodeList';
import { validatePhoneNumber, validateCodeNumber, getParamValByUrl } from '@js/util/util';
import { captchaGetSenseKey } from 'lagou-base-captcha';
import { fetchGetSenseKey, fetchPhoneCode, submitPassWord, fetchPassWordLogin, PHONE_CODE_STATE_LIST, PASSWORD_CODE_STATE_LIST, fetchSubmitToken } from '@service/login';

export default {
    name: 'PCModal',
    components: {
        CountryCodeList
    },
    inject: ['self'],
    props: {
        show: {
            type: Boolean,
            default: false
        }
    },
    data() {
        let unboundPhone = window.unboundPhone;
        const token = getParamValByUrl('token', location.href);
        return {
            token,
            submitCode: '',
            submitToken: '',
            unboundPhone, //
            oauthType: getParamValByUrl('oauthType', location.href),
            activeType: 'MOBILE',
            countryCode: '0086',
            phoneValue: '',
            codeValue: '',
            password: '',
            isShowCountryCode: false,
            phoneValueTip: '',
            codeValueTip: '',
            passwordPhoneTip: '',
            passwordCodeTip: '',
            identifyingCodeSecond: 0,
            identifyingCodeTimer: null,
            isFetching: false,
            isLogin: window.isLogin,
            defaultMsg: '',
        };
    },
    computed: {
        codeTipText() {
            let sec = this.identifyingCodeSecond;
            return sec > 0 ? `${sec}s ` : '';
        },
        isPasswordType() {
            return this.activeType === 'PASSWORD';
        }
    },
    mounted() {
        this.$nextTick(() => {
            window.unboundPhone && (this.self.goLogin());
            window.unboundPhone && (this.querySubmitToken());
            window.unboundPhone && (window.unboundPhone = false);
        });
    },
    beforeDestroy() {
        if (this.identifyingCodeTimer) {
            clearInterval(this.identifyingCodeTimer);
        }
    },
    methods: {
        close() {
            this.unboundPhone = false;
            this.$emit('close');
        },
        clickModal() {
          if (this.isShowCountryCode) {
              this.toggleCountryCode();
          }
        },
        changeType(type) {
            this.activeType = type;
        },
        toggleCountryCode() {
            this.isShowCountryCode = !this.isShowCountryCode;
        },
        selectCountryCode(code) {
            this.countryCode = code;
            this.toggleCountryCode();
        },
        validPassword() {
            let password = this.password;
            let msg = '';
            if (!password) {
              msg = '';
            } else if (!/^.{6,16}$/.test(password)) {
              msg = '6-16';
            }
            this.passwordCodeTip = msg;
            if (msg) {
              return false;
            }
            return true;
        },
        validPhone() {
            let name = this.phoneValue;
            let msg = '';
            if (!name) {
              msg = '';
            } else if (
              !/^\d{5,15}$/.test(name)
            ) {
              msg = '/';
            }
            this.phoneValueTip = msg;
            if (msg) {
                return false;
            }
            return true;
        },
        validUserName() {
            let name = this.phoneValue;
            let msg = '';
            if (!name) {
              msg = '/';
            } else if (
              !/^\d{5,15}$/.test(name) && !/^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/.test(name)
            ) {
              msg = '/';
            }

            this.passwordPhoneTip = msg;

            if (msg) {
                return false;
            }
            return true;
        },
        isVerifiedPhone() {
            if (!validatePhoneNumber(this.phoneValue)) {
                this.$refs.codePhone.focus();
                this.phoneValueTip = '';
                return false;
            }
            return true;
        },
        isVerifiedCode() {
            if (!validateCodeNumber(this.codeValue)) {
                this.codeValueTip = '';
                return false;
            }
            return true;
        },
        startSense() {
            if (!this.isVerifiedPhone() || this.identifyingCodeSecond > 0) return;
            if (this.isFetching) {
                return;
            }
            this.isFetching = true;
            let is_douyin = getParam('utm_source') === 'toutiao-douyin';
            let origin_gtAppId = '66442f2f720bfc86799932d8ad2eb6c7';
            let douyin_gtAppId = '3fc117af5c1a68bb4711684ac0e9be9a';
            captchaGetSenseKey().then(() => {
                window.LaGouCaptchaGetSenseKey({
                    level: 1,
                    gtAppId: is_douyin ? douyin_gtAppId : origin_gtAppId,
                    tcAppId: '2088474689',
                    successCallback: item => {
                        item.tiktok = is_douyin;
                        this.getCode(item);
                    },
                    closeCallback: item => {
                        this.isFetching = false;
                    },
                    errorCallback: error => {
                        this.isFetching = false;
                        console.log('getSenseKey fail', error);
                    },
                    normalCallback: () => {
                        this.getCode();
                    }
                });
            });
        },
        getCode(senseParam) {
            console.log('getCode');
            fetchPhoneCode({
                countryCode: this.countryCode,
                phone: this.phoneValue,
                type: 0,
                ...senseParam
            }).then(res => {
                if (res.state === 1) {
                    this.isFetching = false;
                    this.codeValueTip = '';
                    this.setTimeOfCode();
                    return;
                }
                this.isFetching = false;
                this.codeValueTip = PHONE_CODE_STATE_LIST[res.state].message || this.defaultMsg;
            }).catch(() => {
                this.codeValueTip = this.defaultMsg;
                this.isFetching = false;
            });
        },
        setTimeOfCode() {
            this.identifyingCodeSecond = 60;
            this.identifyingCodeTimer = setInterval(() => {
                this.identifyingCodeSecond--;
                if (this.identifyingCodeSecond === 0) {
                    clearInterval(this.identifyingCodeTimer);
                }
            }, 1000);
        },
        clearTips(key) {
            this[key] && (this[key] = '');
        },
        beforeSubmitVerified() {
            let isPass = true;
            if (this.isPasswordType) {
                if (!this.phoneValue.trim()) {
                    isPass = false;
                    this.passwordPhoneTip = '/';
                }
                if (!this.password.trim()) {
                    isPass = false;
                    this.passwordCodeTip = '';
                }
                if (!this.validPassword()) {
                    isPass = false;
                    this.passwordCodeTip = '6-16';
                }
                if (!this.validUserName()) {
                    isPass = false;
                    this.passwordPhoneTip = '/';
                }
            }
            else {
                if (!this.isVerifiedPhone() || !this.isVerifiedCode()) {
                    isPass = false;
                }
            }
            return isPass;
        },
        submit() {
            if (!this.beforeSubmitVerified() || this.isLogin) return;
            this.isLogin = true;
            let tipKey;
            if (this.isPasswordType) {
                this.handlePasswordPrew();
                tipKey = 'passwordCodeTip';
            }
            else {
                tipKey = 'codeValueTip';
                this.handlePhoneCode().then(res => {
                    if (res.state === 1) {
                        window.open('/frontLogin.do', '_self');
                        return;
                    }
                    this.isLogin = false;
                    this[tipKey] = PHONE_CODE_STATE_LIST[res.state].message || ',';
                }).catch(_ => {
                    this.isLogin = false;
                    this[tipKey] = ',';
                });
            }
        },
        querySubmitToken() {
            axios.get(`${API_BASE_URL}/account/token`).then(({ data = {} }) => {
                console.log(data, 'submitToken');
                if (data.state === 1) {
                    this.submitCode = data.submitCode;
                    this.submitToken = data.submitToken;
                }
            });
        },
        //   
        boundLogin() {
            if (!this.beforeSubmitVerified()) return;
            // this.isLogin = true;
            let tipKey = 'codeValueTip';
            let params = {
                oauthType: this.oauthType,
                credential: this.phoneValue,
                countryCode: this.countryCode,
                phoneVerifyCode: this.codeValue,
                // imageVerifyCode: vm.picCode,
                token: this.token,
                loginType: 1,
                imageVerifyCode: '',
                challenge: 111
            };
            axios.post(`${API_BASE_URL}/account/oauth/0/oldMainAccount`,
            { ...params },
            {
                headers: {
                    'Content-Type': 'application/json;charset=UTF-8',
                    'X-Anit-Forge-Code': this.submitCode,
                    'X-Anit-Forge-Token': this.submitToken
                }
            }
            ).then(({ data = {} }) => {
                console.log(data, 'data');
                if (data.state === 1) {
                   this.self.closeLogin();
                   let deUrl = decodeURIComponent(data.content.refreshTgtUrl);
                   let cbUrl = getParamValByUrl('callback', deUrl);
                   cbUrl += '?productCode=1';
                   let resultUrl = deUrl.replace(/callback[\s\S]*html/, 'callback=' + cbUrl);
                   window.open(resultUrl, '_self');
                } else {
                    this.$message.warning(data.message || '');
                    this.querySubmitToken();
                }
            }).catch(_ => {
                // this.isLogin = false;
                this[tipKey] = '';
                this.querySubmitToken();
            });
        },
        handlePhoneCode() {
            return submitPassWord({
                countryCode: this.countryCode,
                wxinfo: '',
                phone: this.phoneValue,
                phoneVerificationCode: this.codeValue,
            });
        },
        handlePassword({ challenge }) {
            const SALT = 'veenike';
            fetchPassWordLogin({
                isValidate: true,
                username: this.phoneValue,
                password: md5(SALT + md5(this.password) + SALT),
                request_form_verifyCode: '',
                challenge,
            }).then((res) => {
                if (res.state === 1) {
                    window.open('/frontLogin.do', '_self');
                    return;
                }
                this.isLogin = false;
                console.log(res, PASSWORD_CODE_STATE_LIST[res.state], '');
                this.passwordCodeTip = PASSWORD_CODE_STATE_LIST[res.state].message || ',';
            }).catch(_ => {
                this.isLogin = false;
                this.passwordCodeTip = ',';
            });
        },
        async handlePasswordPrew() {
            let _this = this;
            captchaGetSenseKey().then(() => {
                window.LaGouCaptchaGetSenseKey({
                    level: 1, //
                    gtAppId: '66442f2f720bfc86799932d8ad2eb6c7', //appid
                    tcAppId: '2088474689', //appid
                    successCallback: async (item) => {
                    //
                    const res = await _this.handlePassword(item);
                    },
                    closeCallback: (item) => {
                    //
                    console.log('sense close');
                    },
                    errorCallback: (error) => {
                        console.log('getSenseKey fail', error);
                    },
                    normalCallback: async () => {
                        const res = await _this.handlePassword();
                    }
                });
            });
        },
        wechatScan() {
            window.open('https://passport.lagou.com/oauth20/auth_weixinEduProvider.html', '_self');
        }
    }
};
