export default {
    name: 'Toast',
    props: {
        show: {
            type: Boolean,
            default: false
        },
        msg: {
            type: String,
            default: ''
        },
        duration: {
            type: Number,
            default: 1500
        }
    },
    watch: {
        show(value) {
            if (value) {
                setTimeout(() => {
                    this.$emit('onHide');
                }, this.duration);
            }
        }
    }
};
