Vue.use(vueTouch);
Vue.use(VueAxios, axios);

Vue.config.productionTip = false;
const userAgent = navigator.userAgent;
const isPc = !/Android|webOS|iPhone|iPod|iPad|BlackBerry/i.test(userAgent);
const reg = new RegExp(/lagou\/\S*/gi);
const eduReg = new RegExp(/lgedu\/\S*/gi);
const isAPP = isApp();
const isLgApp = reg.test(userAgent);
const isEduApp = eduReg.test(userAgent);
const lagouEduTheme = !isPc && !isLgApp;
// const isEduApp = true;

Vue.mixin({
    data() {
        const ret = {};
        if (this.GLOBAL_DOMAIN) {
            return ret;
        }
        ret.isPc = isPc;
        ret.isApp = isAPP;
        ret.isLgApp = isLgApp;
        ret.isEduApp = isEduApp;
        ret.GLOBAL_DOMAIN = GLOBAL_DOMAIN;
        ret.isLogin = window.isLogin;
        ret.userInfo = window.userInfo;
        ret.lagouEduTheme = lagouEduTheme;
        ret.currentEnvironment = getEnvironment();
        ret.isShowToast = false;
        ret.toastStr = '';
        return ret;
    },
    created() {
        this.saLogin();
    },
    methods: {
        twoPlatTrack(events, props = {}) {
            lgSa.twoPlatTrack(events, Object.assign({
                sid: `${getParam('sid') || ''}`
            }, props));
        },
        // 
        // eduPlatTrack(events, props = {}) {
        //     lgSa.eduPlatTrack(events, Object.assign({
        //         sid: `${getParam('sid') || ''}`
        //     }, props), true);
        // },
        // 
        eduPlatTrack(events, props = {}) {
            lgSa.eduPlatTrack(events, Object.assign({
                sid: `${getParam('sid') || ''}`
            }, props));
        },
        saLogin() {
            let { id } = window.userInfo;
            if (id) {
                window.sa && window.sa.login(id);
            } else {
                let first_id = window.sa && window.sa.store && window.sa.store.getFirstId();
                window.sa && first_id && window.sa.logout(true);
            }
        },
        hideToast() {
            this.isShowToast = false;
        },
        showToast(str) {
            this.toastStr = str;
            this.isShowToast = true;
        },

        wxInfoTrigger() {
            if (browserName != 'WX' || window.userInfo.id) {
                return;
            }
            let urlParamer = getParam();

            if (urlParamer.wxinfo) {
                delete urlParamer.wxinfo;
                return;
            }
            let paramerString = parseObjToUrl(urlParamer);
            let { origin, pathname } = window.location;
            let expandInfo = `${origin}${pathname}?wxinfo=2&${paramerString}`;
            window.location.replace(expandInfo);
        },

        openThirdUrl(url, hideTitle) {
            let _url = '';
            if (hideTitle) {
                hideTitle = 1;
            }
            let hideTitleOption = hideTitle ? 1 : 0;
            if (this.isEduApp) {
                _url = `lgedu://lgedu.com/h5/open?hideTitle=${hideTitleOption}&url=${encodeURIComponent(url)}`;
            }
            else if (this.isLgApp) {
                _url = `lagou://lagou.com/h5/open?hideTitle=${hideTitleOption}&url=${encodeURIComponent(url)}`;
            }
            else {
                let parseUrl = parseURL(url);
                let { host, path, params } = parseUrl;
                let centerLogin = 'www.lagou.com/center/login';
                if (`${host}${path}` === centerLogin && params && params.forward) {
                    _url = decodeURIComponent(params.forward);
                }
                else {
                    _url = url;
                }
            }
            window.location.href = _url;
        },
        setLogin(flag) {
            this.isLogin = flag;
        },
    }
});

Vue.directive('c', {
    bind: function (el, binding, vnode) {
        el.className = isPc ? `${binding.value}-pc` : binding.value;
    }
});
Vue.directive('clickoutside', {
    bind(el, binding, vnode) {
        function documentHandler(e) {
            // 
            if (el.contains(e.target)) {
                return false;
            }
            // 
            if (binding.expression) {
                //  binding.valuehandleClose
                binding.value(e);
            }
        }
        // unbind
        el.__vueClickOutside__ = documentHandler;
        document.addEventListener('click', documentHandler);
    },
    unbind(el, binding) {
        // 
        document.removeEventListener('click', el.__vueClickOutside__);
        delete el.__vueClickOutside__;
    },
});
// 
Vue.directive('preventReClick', {
    bind(el, binding) {
        el.addEventListener('click', () => {
            if (!el.disabled) {
                el.disabled = true;
                setTimeout(() => {
                    el.disabled = false;
                }, binding.value || 2000);
            }
        });
    }
});
let { userInfo = {} } = window;
lgSa.lgSaRegister({
    LGID: Cookies.get('user_trace_token') || '',
    user_id: userInfo.id || userInfo.userId || ''
});
