import javaIcon from '@/assets/launchNav/java.png';
import dataIcon from '@/assets/launchNav/bigData.png';
import webIcon from '@/assets/launchNav/frontend.png';
export default {
    name: 'LaunchNav',
    props: {
        activeTab: {
            type: Number,
            default: 1
        }
    },
    data() {
        return {
            javaIcon,
            dataIcon,
            webIcon,
            campList: [
                { name: 'Java', img: javaIcon, url: 'https://edu.lagou.com/java_basic_sem_search.html#/index' },
                { name: '', img: dataIcon, url: 'https://edu.lagou.com/data_basic_sem_search.html#/index' },
                { name: 'Web', img: webIcon, url: 'https://edu.lagou.com/web_basic_sem_search.html' }
            ]

        };
    },
    methods: {
        linkTo(index) {
            if (this.activeTab !== index) {
                window.open(this.campList[index].url);
            }
        }
    }
};
