            ActivityStyle,
            ModalStyle
        },
        props: {
            // loginType:1:  2:)
            loginType: {
                type: Number,
                default: 1
            },
            show: {
                type: Boolean,
                default: false
            },
            showCloseBtn: {
                type: Boolean,
